import React, { useState, useEffect, useCallback } from 'react';
import {
  Box,
  Typography,
  Button,
  Card,
  CardContent,
  CardActions,
  CircularProgress,
  Divider,
  Avatar,
  Chip,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText,
} from '@mui/material';
import {
  Person as PersonIcon,
  Refresh as RefreshIcon,
  PersonRemove as PersonRemoveIcon,
  AccessTime as AccessTimeIcon,
  ContactPhone as ContactPhoneIcon,
} from '@mui/icons-material';
import { formatDistanceToNow } from 'date-fns';
import { zhCN } from 'date-fns/locale';
import { emergencyCallContactService, emergencyCallRemoveContactService, IEstablishedContactInfo, user } from '@app/model';
import { useProxyWatch } from '@fanfanlo';

// 定义请求处理器类型
type RequestHandler<T = any> = {
  onData: (res: T) => void;
  onError: (err: Error) => void;
};

// 定义 API 响应类型
interface IApiResponse<T = any> {
  shellIsOk: boolean;
  dataIsOk: boolean;
  data?: {
    is_ok: boolean;
    msg?: string;
    contacts?: IEstablishedContactInfo[];
  } & T;
}

// 联系人卡片组件
function ContactCard({
  contact,
  onRemove,
  isProcessing = false,
}: {
  contact: IEstablishedContactInfo;
  onRemove?: (contactId: string) => Promise<void>;
  isProcessing?: boolean;
}) {
  const [isRemoving, setIsRemoving] = useState(false);
  const [showRemoveDialog, setShowRemoveDialog] = useState(false);

  const handleRemove = async () => {
    if (!onRemove) return;

    try {
      setIsRemoving(true);
      await onRemove(contact.contact_id);
      setShowRemoveDialog(false);
    } finally {
      setIsRemoving(false);
    }
  };

  return (
    <>
      <Card variant="outlined" sx={{ mb: 2, opacity: isProcessing ? 0.7 : 1 }}>
        <CardContent>
          <Box display="flex" alignItems="center" mb={1}>
            <Avatar
              src={contact.user_info.avatar_url}
              alt={contact.user_info.display_name}
              sx={{ width: 50, height: 50, mr: 2 }}>
              {contact.user_info.display_name?.[0]?.toUpperCase() || '?'}
            </Avatar>
            <Box flexGrow={1}>
              <Typography variant="h6">{contact.user_info.display_name || '未知用户'}</Typography>
              <Typography variant="body2" color="text.secondary">
                ID: {contact.user_info.user_id}
              </Typography>
              {contact.relationship && (
                <Chip size="small" label={contact.relationship} color="primary" variant="outlined" sx={{ mt: 0.5 }} />
              )}
            </Box>
            <Box textAlign="right">
              <Chip
                size="small"
                icon={<AccessTimeIcon fontSize="small" />}
                label={`${formatDistanceToNow(contact.created_at * 1000, {
                  addSuffix: true,
                  locale: zhCN,
                })}`}
                color="default"
                variant="outlined"
              />
            </Box>
          </Box>

          {contact.notes && (
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
              备注: {contact.notes}
            </Typography>
          )}
        </CardContent>

        <CardActions sx={{ justifyContent: 'flex-end', pt: 0 }}>
          <Button
            size="small"
            color="error"
            variant="outlined"
            startIcon={<PersonRemoveIcon />}
            onClick={() => setShowRemoveDialog(true)}
            disabled={isProcessing || isRemoving}>
            解除关系
          </Button>
        </CardActions>
      </Card>

      {/* 删除确认对话框 */}
      <Dialog
        open={showRemoveDialog}
        onClose={() => setShowRemoveDialog(false)}
        aria-labelledby="remove-contact-dialog">
        <DialogTitle id="remove-contact-dialog">确认解除联系人关系</DialogTitle>
        <DialogContent>
          <DialogContentText>
            您确定要与 <strong>{contact.user_info.display_name}</strong> 解除紧急联系人关系吗？ 此操作不可撤销。
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowRemoveDialog(false)} disabled={isRemoving}>
            取消
          </Button>
          <Button
            onClick={handleRemove}
            color="error"
            variant="contained"
            disabled={isRemoving}
            startIcon={isRemoving ? <CircularProgress size={16} /> : <PersonRemoveIcon />}>
            {isRemoving ? '处理中...' : '确认解除'}
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}

const EstablishedContactsManager: React.FC = () => {
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [isRefreshing, setIsRefreshing] = useState<boolean>(false);
  const [contacts, setContacts] = useState<IEstablishedContactInfo[]>([]);
  const [isProcessing, setIsProcessing] = useState<boolean>(false);

  // 加载已建立的联系人关系
  const loadEstablishedContacts = useCallback(async (showLoading = true) => {
    if (showLoading) {
      setIsLoading(true);
    } else {
      setIsRefreshing(true);
    }
    setError(null);

    try {
      const res = await emergencyCallContactService.listEstablishedContacts({
        params: {},
      } as any);
      if (res.shellIsOk && res.dataIsOk && res.data?.is_ok) {
        setContacts(res.data.contacts || []);
      } else {
        setError(res.data?.msg || '加载联系人列表失败');
      }
    } catch (err) {
      console.error('加载联系人列表异常:', err);
      setError('发生未知错误');
    } finally {
      setIsLoading(false);
      setIsRefreshing(false);
    }
  }, []);

  // 移除联系人关系
  const handleRemoveContact = useCallback(async (contactId: string): Promise<void> => {
    try {
      setIsProcessing(true);

      const res = await emergencyCallRemoveContactService.removeEstablishedContact({
        params: { contact_id: contactId },
        // handler,
      } as any);

      if (!res.shellIsOk || !res.dataIsOk || !res.data?.is_ok) {
        throw new Error(res.data?.msg || '移除联系人失败');
      }
      // 从本地状态中移除
      setContacts(prev => prev.filter(contact => contact.contact_id !== contactId));
    } catch (err) {
      console.error('移除联系人失败:', err);
      setError(err instanceof Error ? err.message : '移除联系人失败，请重试');
    } finally {
      setIsProcessing(false);
    }
  }, []);

  // 处理刷新
  const handleRefresh = useCallback(() => {
    loadEstablishedContacts(false);
  }, [loadEstablishedContacts]);

  // 组件加载时获取数据
  useEffect(() => {
    loadEstablishedContacts();
  }, [loadEstablishedContacts]);

  const renderContent = () => {
    if (isLoading) {
      return (
        <Box display="flex" flexDirection="column" alignItems="center" p={4}>
          <CircularProgress />
          <Typography variant="body2" color="text.secondary" sx={{ mt: 2 }}>
            加载中...
          </Typography>
        </Box>
      );
    }

    if (error) {
      return (
        <Box p={2}>
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
          <Button
            variant="outlined"
            color="primary"
            onClick={() => loadEstablishedContacts()}
            startIcon={<RefreshIcon />}>
            重试
          </Button>
        </Box>
      );
    }

    if (contacts.length === 0) {
      return (
        <Box
          display="flex"
          flexDirection="column"
          alignItems="center"
          justifyContent="center"
          p={4}
          textAlign="center"
          bgcolor="action.hover"
          borderRadius={1}>
          <ContactPhoneIcon color="disabled" sx={{ fontSize: 64, mb: 2 }} />
          <Typography variant="h6" color="textSecondary" gutterBottom>
            暂无紧急联系人
          </Typography>
          <Typography variant="body2" color="textSecondary">
            您还没有建立任何紧急联系人关系
          </Typography>
        </Box>
      );
    }

    return (
      <Box>
        {contacts.map(contact => (
          <ContactCard
            key={contact.contact_id}
            contact={contact}
            onRemove={handleRemoveContact}
            isProcessing={isRefreshing || isProcessing}
          />
        ))}
      </Box>
    );
  };

  return (
    <Box p={2}>
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Box>
          <Typography variant="h5" gutterBottom>
            我的紧急联系人
          </Typography>
          <Typography variant="body2" color="text.secondary">
            管理您已建立的紧急联系人关系
          </Typography>
        </Box>
        <Button
          variant="outlined"
          size="small"
          onClick={handleRefresh}
          disabled={isRefreshing}
          startIcon={isRefreshing ? <CircularProgress size={20} /> : <RefreshIcon />}>
          {isRefreshing ? '刷新中...' : '刷新'}
        </Button>
      </Box>

      {contacts.length > 0 && (
        <Alert severity="info" sx={{ mb: 2 }}>
          共有 {contacts.length} 个紧急联系人
        </Alert>
      )}

      {renderContent()}
    </Box>
  );
};

export default EstablishedContactsManager;
