import React, { useState } from 'react';
import {
  Box,
  Typography,
  Tabs,
  Tab,
  Paper,
  Divider
} from '@mui/material';
import {
  People as PeopleIcon,
  PersonAdd as PersonAddIcon,
  ContactPhone as ContactPhoneIcon
} from '@mui/icons-material';

import InvitationManager from '../InvitationManager/InvitationManager';
import EstablishedContactsManager from '../EstablishedContactsManager/EstablishedContactsManager';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`contact-tabpanel-${index}`}
      aria-labelledby={`contact-tab-${index}`}
      {...other}
    >
      {value === index && <Box>{children}</Box>}
    </div>
  );
}

function a11yProps(index: number) {
  return {
    id: `contact-tab-${index}`,
    'aria-controls': `contact-tabpanel-${index}`,
  };
}

const ContactRelationshipManager: React.FC = () => {
  const [currentTab, setCurrentTab] = useState(0);

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setCurrentTab(newValue);
  };

  return (
    <Box sx={{ width: '100%', maxWidth: 800, mx: 'auto', p: 2 }}>
      {/* 页面标题 */}
      <Box mb={3} textAlign="center">
        <Typography variant="h4" gutterBottom>
          联系人关系管理
        </Typography>
        <Typography variant="body1" color="text.secondary">
          管理您的紧急联系人邀请和已建立的关系
        </Typography>
      </Box>

      <Paper elevation={2}>
        {/* 标签导航 */}
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs 
            value={currentTab} 
            onChange={handleTabChange}
            aria-label="联系人管理标签"
            variant="fullWidth"
          >
            <Tab 
              icon={<ContactPhoneIcon />}
              label="我的联系人" 
              {...a11yProps(0)}
            />
            <Tab 
              icon={<PersonAddIcon />}
              label="邀请管理" 
              {...a11yProps(1)}
            />
          </Tabs>
        </Box>

        {/* 标签内容 */}
        <TabPanel value={currentTab} index={0}>
          <EstablishedContactsManager />
        </TabPanel>
        
        <TabPanel value={currentTab} index={1}>
          <InvitationManager />
        </TabPanel>
      </Paper>
    </Box>
  );
};

export default ContactRelationshipManager;