import { IRequestOptions } from "../IRequestOptions";
import { tms } from "../tms";
import { request } from "@fanfanlo";

/**
 * 获取已建立联系人关系的请求参数
 */
export interface IParamsListEstablishedContacts {
  // 可以添加分页等参数
  page?: number;
  page_size?: number;
}

/**
 * 已建立联系人的用户信息
 */
export interface IEstablishedContactUserInfo {
  user_id: string;
  display_name: string;
  avatar_url?: string;
}

/**
 * 已建立的联系人关系信息
 */
export interface IEstablishedContactInfo {
  contact_id: string;
  user_info: IEstablishedContactUserInfo;
  relationship: string;
  notes?: string;
  created_at: number;
  is_active: boolean;
}

/**
 * 获取已建立联系人关系的响应结果
 */
export interface IResultListEstablishedContacts {
  is_ok: boolean;
  msg?: string;
  contacts: IEstablishedContactInfo[];
}

/**
 * 移除已建立联系人关系的请求参数
 */
export interface IParamsRemoveEstablishedContact {
  contact_id: string;
}

/**
 * 移除已建立联系人关系的响应结果
 */
export interface IResultRemoveEstablishedContact {
  is_ok: boolean;
  msg?: string;
}

/**
 * 获取已建立的联系人关系
 */
export async function tmsEmergencyCallListEstablishedContacts(
  opts: IRequestOptions<IParamsListEstablishedContacts>
) {
  return request<IResultListEstablishedContacts, IParamsListEstablishedContacts>(
    tms.TEmc,
    tms.MEmcListEstablishedContacts,
    opts.params,
    opts.gateway,
    opts.handler
  );
}

/**
 * 移除已建立的联系人关系
 */
export async function tmsEmergencyCallRemoveEstablishedContact(
  opts: IRequestOptions<IParamsRemoveEstablishedContact>
) {
  return request<IResultRemoveEstablishedContact, IParamsRemoveEstablishedContact>(
    tms.TEmc,
    tms.MEmcRemoveEstablishedContact,
    opts.params,
    opts.gateway,
    opts.handler
  );
}

// 导出服务
export const emergencyCallContactService = {
  listEstablishedContacts: tmsEmergencyCallListEstablishedContacts,
  removeEstablishedContact: tmsEmergencyCallRemoveEstablishedContact,
};

export default emergencyCallContactService;